/* ImageGallery */

.image-gallery-container {
  width: 100%;
  padding: 1px 1px 40px 1px;
  margin-top: 1px;
  font-family: 'Roboto Slab', serif;
}

.image-gallery {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-auto-rows: 250px;
  gap: 1px;
  margin-bottom: 30px;
}

.gallery-item {
  overflow: hidden;
  position: relative;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease;
}

.gallery-item:hover {
  transform: scale(1.05);
}

.gallery-item img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: opacity 0.3s ease;
}

.large-item {
  grid-column: span 2;
  grid-row: span 2;
}

.overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(255, 215, 0, 0.7);
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 0;
  transition: opacity 0.3s ease;
}

.gallery-item:hover .overlay {
  opacity: 1;
}

.search-icon {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 48px;
  height: 48px;
  background-color: rgba(255, 255, 255, 0.9);
  border-radius: 50%;
  color: #333;
  transition: background-color 0.3s ease, transform 0.2s ease;
}

.search-icon:hover {
  background-color: #fff;
  transform: scale(1.1);
}

.gallery-nav {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 30px;
}

.nav-button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
  background-color: #ffd700;
  border: none;
  border-radius: 50%;
  color: #333;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease;
  margin: 0 10px;
  padding: 0;
  flex-shrink: 0;
}

.nav-button:hover {
  background-color: #ffea00;
  transform: scale(1.1);
}

.nav-button svg {
  width: 24px;
  height: 24px;
}

.nav-dot {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background-color: #ddd;
  margin: 0 8px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.nav-dot.active {
  background-color: #ffd700;
}

.full-portfolio-btn {
  display: block;
  width: 201px;
  margin: 0 auto;
  padding: 12px 24px;
  background-color: #ffd700;
  border: none;
  border-radius: 25px;
  font-size: 16px;
  font-weight: bold;
  color: #333;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease;
}

.full-portfolio-btn:hover {
  background-color: #ffea00;
  transform: translateY(-2px);
}

