/* Image Lightbox */
.lightbox-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.lightbox-content {
  position: relative;
  width: 80%;
  height: 80%;
  background-color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}

.lightbox-content img {
  max-width: 100%;
  max-height: 100%;
  object-fit: contain;
}

.lightbox-close,
.lightbox-prev,
.lightbox-next {
  position: absolute;
  background: rgba(0, 0, 0, 0.5);
  border: none;
  color: white;
  font-size: 24px;
  cursor: pointer;
  padding: 10px;
  transition: background-color 0.3s;
}

.lightbox-close:hover,
.lightbox-prev:hover,
.lightbox-next:hover {
  background-color: rgba(0, 0, 0, 0.8);
}

.lightbox-close {
  top: 10px;
  right: 10px;
}

.lightbox-prev {
  left: 10px;
  top: 50%;
  transform: translateY(-50%);
}

.lightbox-next {
  right: 10px;
  top: 50%;
  transform: translateY(-50%);
}

html {
  overflow-y: scroll;
}

body {
  overflow-x: hidden;
}

@media screen and (max-width: 768px) {
  .image-gallery-container {
    padding: 1px 1px 35px 1px;
  }

  .image-gallery {
    grid-auto-rows: 200px;
    margin-bottom: 25px;
  }

  .search-icon {
    width: 40px;
    height: 40px;
  }

  .gallery-nav {
    margin-bottom: 25px;
  }

  .nav-button {
    width: 35px;
    height: 35px;
    margin: 0 8px;
  }

  .nav-button svg {
    width: 20px;
    height: 20px;
  }

  .nav-dot {
    width: 10px;
    height: 10px;
    margin: 0 6px;
  }
}

@media screen and (max-width: 480px) {
  .image-gallery-container {
    padding: 1px 1px 30px 1px;
  }

  .image-gallery {
    grid-template-columns: repeat(2, 1fr);
    grid-auto-rows: 150px;
    margin-bottom: 20px;
  }

  .search-icon {
    width: 36px;
    height: 36px;
  }

  .gallery-nav {
    margin-bottom: 20px;
  }

  .nav-button {
    width: 30px;
    height: 30px;
    margin: 0 6px;
  }

  .nav-button svg {
    width: 18px;
    height: 18px;
  }

  .nav-dot {
    width: 8px;
    height: 8px;
    margin: 0 5px;
  }
}