@import url('https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@300;400;700&display=swap');

.meet-happy-clients {
  font-family: 'Roboto Slab', serif;
  text-align: center;
  padding: 2rem 2rem;
  background-image: url('../../images/happyclient.jpg');
  background-size: cover;
  background-position: center;
  position: relative;
}

.meet-happy-clients::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(255, 255, 255, 0.85);
}

.client-title {
  font-size: 2.5rem;
  margin-bottom: 0.5rem;
  position: relative;
  z-index: 1;
  font-weight: 300;
  color: #333;
}

.client-title::after {
  content: '';
  display: block;
  width: 50px;
  height: 3px;
  background-color: #FFB901;
  margin: 10px auto 0;
}

.section-subtitle {
  font-size: 1.2rem;
  margin-bottom: 2rem;
  position: relative;
  z-index: 1;
  font-weight: 300;
  color: #666;
}

.highlight {
  font-weight: 700;
  color: #333;
}

.testimonial-carousel {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  z-index: 1;
  max-width: 800px;
  margin: 0 auto;
}

.carousel-button {
  background: none;
  border: none;
  cursor: pointer;
  padding: 0;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  color: #868690;
}

.carousel-button::before {
  content: '';
  display: block;
  width: 15px;
  height: 15px;
  border-top: 2px solid currentColor;
  border-right: 2px solid currentColor;
}

.carousel-button.prev {
  left: -40px;
}

.carousel-button.next {
  right: -40px;
}

.carousel-button.prev::before {
  transform: rotate(-135deg);
}

.carousel-button.next::before {
  transform: rotate(45deg);
}

.testimonial {
  max-width: 600px;
  margin: 0 auto;
}

