.testimonial-text {
  font-size: 1.1rem;
  line-height: 1.6;
  margin-bottom: 1rem;
  font-weight: 300;
  color: #333;
}

.author-name {
  font-weight: 600;
  margin-bottom: 0.2rem;
  color: #868690;
  font-style: italic;
}

.author-position {
  font-style: italic;
  color: #868690;
  font-weight: 300;
}
.avatar {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
}

.star-container {
  display: flex;
  justify-content: center;
  align-items: center;
}

.star-icon {
  color: #ffd700; /* Gold color for stars */
  margin: 0 2px; /* Space between stars */
  transition: all 0.3s ease;
}

/* Smallest stars (outermost) */
.star-icon:nth-child(1),
.star-icon:nth-child(5) {
  font-size: 20px;
}

/* Medium stars */
.star-icon:nth-child(2),
.star-icon:nth-child(4) {
  font-size: 24px;
}

/* Largest star (middle) */
.star-icon:nth-child(3) {
  font-size: 32px;
}

.star-icon:hover {
  color: #ffaa00; /* Slightly darker gold on hover */
  filter: drop-shadow(0 0 5px rgba(255, 215, 0, 0.7));
}

@keyframes twinkle {
  0%, 100% {
    opacity: 1;
  }
  50% {
    opacity: 0.7;
  }
}

.star-container:hover .star-icon {
  animation: twinkle 1s infinite;
}

/* Stagger the twinkle animation for each star */
.star-container:hover .star-icon:nth-child(1) { animation-delay: 0s; }
.star-container:hover .star-icon:nth-child(2) { animation-delay: 0.1s; }
.star-container:hover .star-icon:nth-child(3) { animation-delay: 0.2s; }
.star-container:hover .star-icon:nth-child(4) { animation-delay: 0.3s; }
.star-container:hover .star-icon:nth-child(5) { animation-delay: 0.4s; }


@media (max-width: 768px) {
  .client-title {
    font-size: 2rem;
  }

  .section-subtitle {
    font-size: 1rem;
  }

  .testimonial-text {
    font-size: 1rem;
  }

  .carousel-button {
    display: none;
  }
}

@media (max-width: 480px) {
  .client-title {
    font-size: 1.8rem;
  }

  .testimonial {
    margin: 0 10px;
  }
}
