/* BeforeAfterSlider.css */
.before-after-slider {
    position: relative;
    width: 100%;
    height: 300px;
    overflow: hidden;
  }
  
  .before-image,
  .after-image {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-size: cover;
    background-position: center;
  }
  
  .slider {
    position: absolute;
    top: 0;
    bottom: 0;
    width: 4px;
    background: #fff;
    cursor: ew-resize;
  }
  
  .slider-button {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 30px;
    height: 30px;
    border-radius: 50%;
    background: #fff;
    transform: translate(-50%, -50%);
    cursor: ew-resize;
  }
  
  .slider-button::before,
  .slider-button::after {
    content: '';
    position: absolute;
    top: 50%;
    width: 0;
    height: 0;
    border-top: 6px solid transparent;
    border-bottom: 6px solid transparent;
  }
  
  .slider-button::before {
    left: 50%;
    border-right: 6px solid #333;
    transform: translate(-150%, -50%);
  }
  
  .slider-button::after {
    right: 50%;
    border-left: 6px solid #333;
    transform: translate(150%, -50%);
  }